<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process17"></a>

<p><br></p>
<p><span class="rvts17">塞碧莉（Sebille）:</span></p>
<p><img width="371" height="664" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber57.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">塞碧莉曾被她的主人奴役。由于她的主人可以使用魔法歌曲控制她，因此她曾被迫捕杀自己的族人。</span>
<br><span class="rvts21">现在虽然不再是一个奴隶，但是塞碧莉还没有摆脱主人用来控制她的噩梦。作为一名杰出的刺客，是时候要把主人送进坟墓了。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族：精灵族</span>
<br><span class="rvts21">默认职业：刺客（</span><span class="rvts20">Rogue</span><span class="rvts21">）</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">塞碧莉拥有精灵族的起始天赋。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">塞碧莉只能被选为女性，并附带塞碧莉特有标签。该标签可以使得她拥有一些特定的对话和行动。另外她还具备法外之徒和学者的标签。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="135" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="280" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="136" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="600" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="135" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber58.jpg"></p>
</td>
<td width="280" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Flesh Sacrifice</span></p>
</td>
<td width="136" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">鲜血献祭</span></p>
</td>
<td width="600" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">失去</span><span class="rvts20">1</span><span class="rvts21">点体质值，提升</span><span class="rvts20">1</span><span class="rvts21">点</span><span class="rvts20">AP</span><span class="rvts21">值，</span>
<br><span class="rvts21">并增加</span><span class="rvts20">10%</span><span class="rvts21">的额外伤害，持续</span><span class="rvts20">2</span><span class="rvts21">轮</span></p>
</td>
</tr>
<tr valign="top">
<td width="135" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber59.jpg"></p>
</td>
<td width="280" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Break the Shackles</span></p>
</td>
<td width="136" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">打破桎梏</span></p>
</td>
<td width="600" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">移除身上的负面效果，其中包括：</span>
<br><span class="rvts21">减速，残疾，战栗，震击，萎缩，窒息，</span>
<br><span class="rvts21">疾病，感染，桎梏之痛</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">由于鲜肉献祭技能会减少玩家的体质，但是配合</span><span class="rvts20"></span><span class="rvts21">血帜</span><span class="rvts20"></span><span class="rvts21">天赋，以及死灵派系的回血技能，可以恢复生命。</span>
<br><span class="rvts20">2. </span><span class="rvts21">术士天赋与肉体献祭的结合可以造成高爆发的伤害，因此建议塞碧莉使用骑士或战士的职业，利用双手攻击的优势，可在游戏前期对敌人造成强大的压制。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



